.todoapp {
  background: #fff;
  margin: 130px 0 40px 0;
  position: relative;
  box-shadow: 0 2px 4px 0 color(#000 a(0.2)),
              0 25px 50px 0 color(#000 a(0.1));
  input{
    &:-webkit-input-placeholder, &:-moz-placeholder &:input-placeholder{
      font-style: italic;
      font-weight: 300;
      color: #e6e6e6;
    }
  }
}

.new-todo,
.edit {
  position: relative;
  margin: 0;
  width: 100%;
  font-size: 24px;
  font-family: inherit;
  font-weight: inherit;
  line-height: 1.4em;
  border: 0;
  outline: none;
  color: inherit;
  padding: 6px;
  border: 1px solid #999;
  box-shadow: inset 0 -1px 5px 0 color(#000 a(0.2));
  box-sizing: border-box;
  font-smoothing: antialiased;
}

.new-todo {
  padding: 16px 16px 16px 60px;
  border: none;
  background: color(#000 a(0.003));
  box-shadow: inset 0 -2px 1px color(#000 a(0.003));
}



